
.root {
  padding-top: 2.5rem;
  outline: 0;
}
.root:first-child {
  padding-top: 3.75rem;
}
@media print {
.root {
    padding-top: 0.625rem;
}
}
.body {
  padding: 0 5.46875vw;
}
@media print {
.body {
    padding-right: 0;
    padding-left: 0;
}
}
.intersect {
  position: relative;
  z-index: 1;
  display: block;
  width: 100%;
  height: 10px;
  pointer-events: none;
}
.intersect--top {
  composes: intersect;
  margin-top: -1px;
  -webkit-transform: translateY(30vh);
          transform: translateY(30vh);
}
.intersect--bottom {
  composes: intersect;
  margin-bottom: -1px;
  -webkit-transform: translateY(-60vh);
          transform: translateY(-60vh);
}




