/* ==========================================================================

   Base Styles
   ====


   Some Basic Styles
   ========================================================================== */

*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

html,
button,
input,
select,
textarea {
  color: #4d4d4d;
}

html {
  /*
    $font-min: $font-size-default;
    //$font-min: 14px;
    //$font-max: 18px;
    $font-max: 20px;
    $font-diff: ($font-max - $font-min);
    //$breakpoint-min: 1024px;
    $breakpoint-min: 1280px;
    $breakpoint-max: 2560px;
    //$breakpoint-max: 1920px;
    $breakpoint-diff: ($breakpoint-max - $breakpoint-min);
    $font-scale: $font-diff / $breakpoint-diff;
    */
  font-size: 100%;
}

@media (min-width: 64em) {
  html {
    font-size: calc(0.875em + (2 * (100vw - 1024px) / 256));
  }
}

@media (min-width: 80em) {
  html {
    font-size: calc(1em + (4 * (100vw - 1280px) / 1280));
  }
}

body {
  overflow: hidden;
  min-width: 320px;
  height: 100vh;
  margin: 0;
  padding: 0;
  background: #fff;
  color: #4d4d4d;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-family: "Open Sans", sans-serif;
  line-height: 1.25;
}

::-moz-selection {
  background-color: rgba(160, 44, 44, 0.2);
  color: #641c1c;
  text-shadow: none;
}

::selection {
  background-color: rgba(160, 44, 44, 0.2);
  color: #641c1c;
  text-shadow: none;
}

/*
::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-thumb {
    background: darken($color-link, 10%);
}

::-webkit-scrollbar-track {
    background: $color-link;
}
*/

audio,
canvas,
iframe,
svg,
video,
img {
  vertical-align: middle;
}

hr {
  display: block;
  height: 1px;
  margin: 1rem 0;
  padding: 0;
  border: 0;
  border-top: 1px solid #4d4d4d;
}

fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

textarea {
  resize: vertical;
}

input::-moz-focus-inner {
  border: 0;
}

/* ==========================================================================
   ##prevent 300ms delay in FF, Chrome and IE on touch devices
   ========================================================================== */

a[href],
button {
  touch-action: manipulation;
}

/* ==========================================================================
   ##Headings and Base

   1 to 6 heading

   ```
   ```
   ========================================================================== */

h1,
:global(.h1) {
  margin: 0 0 0.2em;
  font-family: "Bree Serif", serif;
  font-size: 2.5rem;
  font-weight: 400;
  line-height: 1.2;
}

h2,
:global(.h2) {
  margin: 0 0 0.2em;
  font-family: "Bree Serif", serif;
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.2;
}

h3,
:global(.h3) {
  margin: 0 0 0.2em;
  font-family: "Bree Serif", serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.2;
}

h4,
:global(.h4) {
  margin: 0 0 0.2em;
  font-family: "Bree Serif", serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.2;
}

h5,
:global(.h5) {
  margin: 0 0 0.2em;
  font-family: "Bree Serif", serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.2;
}

h6,
:global(.h6) {
  margin: 0 0 0.2em;
  font-family: "Bree Serif", serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.2;
}

p {
  margin: 0 0 0.2em;
  line-height: 1.2;
}

abbr[title] {
  border-bottom: 0;
}

:global(.no-touchevents) abbr[title] {
  border-bottom: 1px dotted currentColor;
  text-decoration: none;
}

@media print {
  :global(.no-touchevents) abbr[title] {
    border-bottom: 0;
  }
}

:global(a.u-link--mark),
:global(a:not([class])) {
  background-image: linear-gradient(to top, rgba(77, 77, 77, 0.2) 0, rgba(77, 77, 77, 0.2) 3px, transparent 4px);
  color: inherit;
  outline: 0;
  text-decoration: none;
}

:global(.no-touchevents a.u-link--mark),
:global(.no-touchevents a:not([class])) {
  position: relative;
  display: inline-block;
  padding-right: 0.22em;
  padding-left: 0.22em;
  background-image: none;
  color: inherit;
  outline: 0;
  text-decoration: none;
  transition: all 0.27s;
}

:global(.no-touchevents a.u-link--mark)::after,
:global(.no-touchevents a:not([class]))::after {
  position: absolute;
  right: 0;
  bottom: -0.17em;
  left: 0;
  z-index: -2;
  opacity: 0.1;
  height: 3px;
  background-color: currentColor;
  content: "";
  pointer-events: none;
  transition: all 0.1s linear;
}

:global(.no-touchevents a.u-link--mark)::before,
:global(.no-touchevents a:not([class]))::before {
  position: absolute;
  top: 0;
  bottom: -0.17em;
  left: 0;
  z-index: 1;
  opacity: 0;
  width: 0;
  background-color: #a02c2c;
  content: "";
  pointer-events: none;
  transform: scaleX(1);
  transform-origin: 0 0;
  transition: opacity 0.25s, width 0s ease 0.25s, transform 0s ease 0.25s, transform-origin 0s ease 0.25s;
}

:global(.no-touchevents a.u-link--mark):hover,
:global(.no-touchevents a:not([class])):hover {
  color: #a02c2c;
  text-decoration: none;
  transition-delay: 0.25s;
  transition-duration: 0;
}

:global(.no-touchevents a.u-link--mark):hover::before,
:global(.no-touchevents a:not([class])):hover::before {
  opacity: 1;
  width: 100%;
  transform: scaleX(0);
  transform-origin: right 0;
  transition: width 0.25s ease, transform-origin 0s ease 0.25s, transform 0.25s ease 0.25s;
}

:global(.no-touchevents a.u-link--mark):hover::after,
:global(.no-touchevents a:not([class])):hover::after {
  opacity: 0.3;
  transition: opacity 0.15s ease 0.15s;
}

[data-whatinput="keyboard"] a:not([class]) {
  outline: 2px solid rgba(160, 44, 44, 0);
  outline-offset: 4px;
  transition: outline-offset 0.2s ease, color 0.2s ease;
}

[data-whatinput="keyboard"] a:not([class]):focus {
  color: #a02c2c;
  outline-color: rgba(160, 44, 44, 0.2);
  outline-offset: 2px;
}

[data-whatinput="keyboard"] a:not([class]):focus::before,
[data-whatinput="keyboard"] a:not([class]):focus::after {
  opacity: 0;
}

/* ==========================================================================
   ## Layout settings
   ========================================================================== */

:global [data-scrollbar] .scrollbar-track {
  background: #a02c2c !important;
}

@media print {
  :global [data-scrollbar] .scrollbar-track {
    display: none !important;
  }
}

:global [data-scrollbar] .scrollbar-track-y {
  opacity: 1 !important;
}

:global [data-scrollbar] .scrollbar-track-x {
  display: none !important;
}

:global [data-scrollbar] .scrollbar-thumb {
  background: #782121 !important;
}

/* ==========================================================================

   Utilities
   ====

   ========================================================================== */

/* ==========================================================================
   Section Title

   ```
   <p class="u-text--left">Left aligned text</p>
   <p class="u-text--right">Right aligned text</p>
   <p class="u-text--center">Centered text</p>
   <p class="u-text--hide">Hidden text</p>
   <p class="u-text--ellipsis">This text is one line with ellipsis</p>
   ```
   ========================================================================== */

:global .u-text--left {
  text-align: left !important;
}

:global .u-text--right {
  text-align: right !important;
}

:global .u-text--center {
  text-align: center !important;
}

:global .u-text--hide {
  border: 0;
  background-color: transparent;
  color: transparent;
  font: 0 / 0 a;
  font-size: 0.1px;
  text-shadow: none;
}

:global .u-text--ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

/* ==========================================================================
   ##Box model helpers

   ```

   <div class="u-clearfix">Clearfixed box</div>
   <div class="u-centered">Centered</div>
   <div class="u-float--left">Floated left</div>
   <div class="u-float--right">Floated right</div>
   <div class="u-clear">Clearing box</div>

   <div class="u-inline">
    <div>inlined</div>
    <div>items</div>
   </div>

   <div class="u-stacked">
    <span>stacked</span>
    <span>items</span>
   </div>
   ```
   ========================================================================== */

:global .u-clearfix::before,
:global .u-clearfix::after {
  display: table;
  content: " ";
}

:global .u-clearfix::after {
  clear: both;
}

:global .u-clear {
  clear: both !important;
}

:global .u-centered {
  float: none !important;
  margin-right: auto !important;
  margin-left: auto !important;
}

:global .u-float,
:global .u-float--left {
  float: left !important;
}

:global .u-float--right {
  float: right !important;
}

:global .u-inline > * {
  display: inline-block !important;
}

:global .u-stacked > * {
  display: block !important;
}

:global .u-inline--baseline > * {
  display: inline-block !important;
  vertical-align: baseline !important;
}

/* ==========================================================================
   ##Typographic Helpers

   ```
   <p class="u-type--xs">font size extra small</p>
   <p class="u-type--s">font size small</p>
   <p class="u-type--m">font size medium (default)</p>
   <p class="u-type--l">font size large</p>
   <p class="u-type--xl">font size extra large</p>
   <p class="u-type--xxl">font size oversized</p>

   <p class="u-type--larger">relative size</p>
   <p class="u-weight--strong">strong font weight</p>
   <p class="u-weight--medium">medium font weight</p>
   <p class="u-weight--light">light font weight</p>
   ```
   ========================================================================== */

:global .u-type--xl {
  font-size: 2.5rem !important;
}

:global .u-type--l {
  font-size: 2rem !important;
}

:global .u-type--m {
  font-size: 1rem !important;
}

:global .u-type--s {
  font-size: 0.875rem !important;
}

:global .u-type--xs {
  font-size: 0.625rem !important;
}

:global .u-type--default {
  font-size: 1rem !important;
}

:global .u-type--larger {
  font-size: 130% !important;
}

:global .u-type--smaller {
  font-size: 85% !important;
}

:global .u-weight--regular {
  font-weight: 400 !important;
}

:global .u-weight--semibold {
  font-weight: 600 !important;
}

:global .u-weight--bold {
  font-weight: 700 !important;
}

:global .u-type--mark {
  position: relative;
  display: inline-block;
  padding: 0 0.2em 0.1em;
  background-color: transparent;
  color: #fff;
  line-height: 1;
}

:global .u-type--mark::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  background-color: #a02c2c;
  content: "";
}

/* ==========================================================================

   ### Visibility Helpers

   ```
   <p class="u-hidden">this is hidden from mobile screens</p>
   <p class="u-visuallyhidden">this is hidden from small tablet screens</p>
   <p class="u-invisible">this is hidden from large tablets screens</p>
   ```
   ========================================================================== */

:global .u-hidden,
:global .js .is-jshidden {
  display: none !important;
  visibility: hidden !important;
}

:global .u-visuallyhidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

:global .u-visuallyhidden .focusable:active,
:global .u-visuallyhidden .focusable:focus {
  position: static;
  overflow: visible;
  clip: auto;
  width: auto;
  height: auto;
  margin: 0;
}

:global .u-invisible {
  visibility: hidden !important;
}

@media (min-width: 30em) {
  :global .u-hidden--from-mobile {
    display: none !important;
    visibility: hidden !important;
  }
}

