
.root {
  display: inline-block;
  color: currentColor;
  text-decoration: none;
}
.root:focus,
.root:active {
  outline: 0;
}
:global([data-whatinput="keyboard"]) .root:focus,
:global([data-whatinput="keyboard"]) .root:active {
  outline: 2px solid rgba(160, 44, 44, 0);
  outline-offset: 4px;
  -webkit-transition: outline-offset 0.2s ease, color 0.2s ease;
  transition: outline-offset 0.2s ease, color 0.2s ease;
}
:global([data-whatinput="keyboard"]) .root:focus:focus,
:global([data-whatinput="keyboard"]) .root:active:focus {
  color: #a02c2c;
  outline-color: rgba(160, 44, 44, 0.2);
  outline-offset: 2px;
}
:global([data-whatinput="keyboard"]) .root:focus:focus::before,
:global([data-whatinput="keyboard"]) .root:focus:focus::after,
:global([data-whatinput="keyboard"]) .root:active:focus::before,
:global([data-whatinput="keyboard"]) .root:active:focus::after {
  opacity: 0;
}
.ico {
  display: inline-block;
  line-height: 1;
  text-align: center;
}
.label {
  position: relative;
  display: inline-block;
}
.root--cursor .label {
  margin-left: 0.2em;
  padding-bottom: 0.2em;
  font-weight: inherit;
}
.root--cursor .label::before {
  position: relative;
  bottom: -0.1em;
  display: inline-block;
  width: 0.5em;
  margin-right: 0.1em;
  border-bottom: 2px solid currentColor;
  content: "";
}
@media print {
.root--cursor .label::before {
    display: none;
}
}
:global(.no-touchevents) .root--cursor:hover .label::before {
  color: #a02c2c;
  -webkit-animation-name: blinking-cursor;
          animation-name: blinking-cursor;
  -webkit-animation-duration: 0.7s;
          animation-duration: 0.7s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
@-webkit-keyframes blinking-cursor {
0% {
    opacity: 1;
}
30% {
    opacity: 0;
}
50% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes blinking-cursor {
0% {
    opacity: 1;
}
30% {
    opacity: 0;
}
50% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}




