
.root {
  position: relative;
  z-index: -1;
  margin: 0;
}
@media (min-width: 64em) {
.root {
    -webkit-transform: translate(-16.6666666667%, -16.6666666667%);
            transform: translate(-16.6666666667%, -16.6666666667%);
}
}
@media not print {
:global(.canvas) .fallback {
    position: absolute;
    overflow: hidden;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
}
}
:global(.no-canvas) .canvas {
  display: none;
}
@media print {
.canvas {
    display: none;
}
}

