
.root {
  position: relative;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.item {
  font-size: 1rem;
  font-weight: 400;
  margin-bottom: 0.3em;
  line-height: 1.4em;
}
@media (min-width: 64em) {
.item {
    font-size: 1.125rem;
}
}
.item:last-child {
  margin-bottom: 0;
}
.item > * {
  vertical-align: middle;
}
.item [class*=" ico-"] {
  display: inline-block;
  margin-right: 0.2em;
  font-size: 1.2em;
}




