}
}
@media print {
.root {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    max-width: none;
}
.root::before,
  .root::after {
    display: table;
    content: " ";
}
.root::after {
    clear: both;
}
}
.root > * {
  margin-bottom: 2.5rem;
}
@media print {
.root > * {
    width: calc(50% - rem(20px));
