
.root {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 40%;
  z-index: -1;
}
.root > canvas {
  float: right;
}
@media print {
.root {
    position: absolute;
    bottom: auto;
    left: 0;
    opacity: 1 !important;
    height: 29.7cm;
}
.root::before {
    background-size: auto, auto;
}
.root::after,
  .root > span {
    display: none;
}
}
.root--enter {
  opacity: 0;
}
.root--enter-active {
  -webkit-transition-delay: 1000ms;
          transition-delay: 1000ms;
}
.root--leave {
  opacity: 0;
}




