
.root {
  margin-top: 0;
  color: inherit;
  font-family: "Bree Serif", serif;
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.46875em;
}
.prefix {
  position: relative;
  display: inline-block;
  margin-right: 0.05em;
  padding-right: 0.3125em;
  padding-left: 0.3125em;
  color: #fff;
  font-weight: inherit;
}
.prefix::before {
  position: absolute;
  top: 10%;
  left: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 90%;
  background-color: #a02c2c;
  content: "";
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform-origin: 0 50%;
          transform-origin: 0 50%;
}
.root--sub {
  composes: root;
  margin-top: 0.8em;
  font-size: 1rem;
}
@media (min-width: 64em) {
.root--sub {
    margin-bottom: -1.8em;
}
}
@media print {
.root--sub {
    margin-bottom: 1em;
}
}




